.container{
    
    height: 400px;
    border: 2px solid rgb(58, 97, 253);
    border-radius: 5px;
    
    margin: 10px;
    
    
}
.nav1{
    display: flex;
    
    align-items: center;
    justify-content: space-between;
    height: 50px;
    background-color: rgb(58, 97, 253);
    
    padding: 5px;
    
    
    
}
.slip-logo{
    margin: 10px;
    width: 30px;
        height: 30px;
        border-radius: 50%;
        background-color: white;

    

}

.nav-item{
    display: flex;
    align-items: center;


}
.slider{
    
    width: 100px;
    height: 6px;
    background-color: rgba(62, 60, 60, 0.405);
    
    border-radius: 24px;
    border: 2px solid white;
    
}
.nav-item-button{
    
    
    margin: 5px;
    height: 20px;
    padding: 5px 8px;
    border-radius:  24px ;
    background-color: white;
}
.nav-icons{
    display: flex;
    align-items: center;
    >p{
        color: white;
    }
    .inner-slider{
       
        width: 75px;
        height: 8px;
        background-color: white;
        
    }
   
    
}
.nav-icons-button{
    
    border-radius: 5px;
    background-color: white;
    padding: 3px;
    margin: 3px;
    color: blue;
    font-size: 23px;
    
}